@import url('https://fonts.cdnfonts.com/css/darwin-pro?styles=47877');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: #3F4E55;
}

.shadow-border {
  position: relative;
  box-shadow: 0 0 5px rgba(0, 0, 0, 15.5); /* Example shadow */
}

.App-container {
  display: flex;
  height: 100vh;
}

.div-60 {
  min-width: 60%;
  background-color: #3F4E55;
  height: 100%;
}

.div-30 {
  min-width: 30%;
  background-color: #3F4E55;
  height: 100%;
}

.div-10 {
  min-width: 10%;
  background-color: #3F4E55;
  height: 100%;
}

.shadow-border.div-60 {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  width: 60%;
  background-color: #3F4E55;
  height: 100%;
}

.logoTitle {
  display: flex;
  justify-content: center; /* Center the items horizontally, if desired */
  align-items: center; /* Align the items vertically in the center, if desired */
}

h1 {
  font-size: 2.5em;
  color: #ffffff;
  line-height: 0.5;
}

h2 {
  font-size: 2.5em;
  color: #A7B84C;
  line-height: 0.5;
  font-family: 'Darwin Pro', sans-serif;
}

h3{
  font-size: 1.5em;
  color: #ffffff;
  line-height: 0.5;
}


.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.horizontal-center {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/* Add this CSS to your stylesheet to apply a border to the Link component */
.link-border {
  border: 1px solid #b7c84c; /* Adjust color and border size as needed */
  padding: 8px; /* Adjust padding as needed */
  display: inline-block; /* To ensure padding and border are applied correctly */
  text-decoration: none; /* Remove underline from links */
  color: #b7c84c; /* Adjust color as needed */
  margin-top: 10px;
  margin-bottom: 10px;
  border-left: none;
  border-radius: 0px 5px 5px 0px;
}

.link-border:hover {
  background-color: #657144; /* Adjust color as needed */
}

.description {
  color: white;
  margin: 20px;
  padding: 0;
}

.button_reset{
  background-color: #A7B84C;
  color: white;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px;
}

.how_use2{
  display: none;
}

.how_use{
  display: block;
  display: flex;
  align-items: center;
}

.descr2 {
  display: flex;
}

.descr3 {
  display: none;
}


/*add media*/
@media (max-width: 768px) {

  .div-10-task{
    display: flex;
    justify-content: right;
    align-items: center;
    background-color: #3F4E55;
    height: 30%;
  }

  .descr2 {
    display: none;
  }
  
  .descr3 {
    display: flex;
    align-items: center;
  }
  
  .how_use2{
    display: block;
    display: flex;
    align-items: center;
  }

  .how_use{
    display: none;
  }

  .App-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }

  .div-60 {
    background-color: #3F4E55;
    min-height: 20%;
  }

  .div-30 {
    background-color: #3F4E55;
    min-height: 50%;
    overflow-y: auto;
}

  .div-10 {
    background-color: #3F4E55;
    min-height: 20%;
    display: flex;
    align-items: center;
  }


  .shadow-border.div-60 {
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    width: 100%;
    background-color: #3F4E55;
  }

  .logoTitle {
    display: flex;
    justify-content: center; /* Center the items horizontally, if desired */
    align-items: center; /* Align the items vertically in the center, if desired */
  }

  .img_logo{
    width: 80%;
  }

  h1 {
    font-size: 1.5em;
    color: #ffffff;
    line-height: 0.5;
  }

  h2 {
    font-size: 1.5em;
    color: #A7B84C;
    line-height: 0.5;
    font-family: 'Darwin Pro', sans-serif;
  }

  h3{
    font-size: 1.5em;
    color: #ffffff;
    line-height: 0.5;
  }

  .vertical-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }

  .horizontal-center {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  /* Add this CSS to your stylesheet to apply a border to the Link component */
  .link-border {
    border: 1px solid #b7c84c; /* Adjust color and border size as needed */
    padding: 8px; /* Adjust padding as needed */
    display: inline-block; /* To ensure padding and border are applied correctly */
    text-decoration: none; /* Remove underline from links */
    color: #b7c84c;
    margin-top: 10px;
  }
}

