.inscription{
    font-size: 15px;
    color: white;
    font-weight: bold;
    margin-left: 20px;
}

.title_of_task{
    font-size: 15px;
    color: rgba(255, 255, 255, 0.863);
    text-align: right;
    font-weight: bold;
    margin-right: 0;
}

.description{
    font-size: 15px;
    color: rgba(255, 254, 254, 0.863);
    font-weight: bold;
    margin-right: 0;
}

/*add media*/
@media (max-width: 900px) {
    .inscription{
        font-size: 13px;
    }

    .title_of_task{
        font-size: 13px;
        color: rgba(255, 255, 255, 0.863);
        font-weight: bold;
        margin-right: 0;
    }

    .description{
        font-size: 13px;
        color: rgba(255, 254, 254, 0.863);
        font-weight: bold;
        margin-right: 0;
    }

}

.subTitle{
    color: #b7c84c;
}


